.shown-till-md {
  display: flex;
}
@media (min-width: 900px) {
  .shown-till-md {
    display: none;
  }
}

.shown-from-md {
  display: none !important;
}
@media (min-width: 900px) {
  .shown-from-md {
    display: inline;
  }
}

.shown-from-lg {
  display: none;
}
@media (min-width: 1360px) {
  .shown-from-lg {
    display: inline;
  }
}

body, input {
  font-size: 1.125rem;
  line-height: 1.825;
  font-family: "Surveyor SSm A", "Surveyor SSm B";
  font-style: normal;
  font-weight: 300;
  color: darkBlue;
}

label {
  font-family: "Gotham Light";
  text-transform: uppercase;
  letter-spacing: 1px;
  font-size: 0.75rem;
}

a {
  color: #17386e;
  text-decoration: none;
  border-bottom: 1px solid #17386e;
}

h1 {
  font-size: 1.45rem;
  line-height: 1.125;
  font-family: "Surveyor Display A", "Surveyor Display B";
  font-style: normal;
  font-weight: 500;
  font-size: 2.3rem;
  line-height: 1.075;
  margin-bottom: 3.5rem;
}

h3 {
  font-family: Gotham Bold;
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
ul li {
  padding: 0;
  margin: 0;
}
ul li.year_breaker {
  font-size: 1.25rem;
  font-weight: 600;
  margin-top: 2rem;
}

html, body {
  margin: 0;
  padding: 0;
}

html {
  font-size: 90%;
}
@media (min-width: 1160px) {
  html {
    font-size: 100%;
  }
}

body {
  background: white;
  width: 100vw;
  overflow-x: hidden;
}

.noscript {
  display: none;
}

.container {
  width: calc(100% - 2rem);
  position: relative;
  margin: 0 auto;
  flex-direction: column;
}
@media (min-width: 600px) {
  .container {
    width: calc(100% - 4rem);
  }
}
@media (min-width: 900px) {
  .container {
    width: 820px;
  }
}
@media (min-width: 1160px) {
  .container {
    width: 900px;
  }
}
@media (min-width: 1360px) {
  .container {
    width: 920px;
  }
}

.full-container {
  width: 100%;
}
@media (min-width: 600px) {
  .full-container {
    width: calc(100% - 2rem);
  }
}
@media (min-width: 900px) {
  .full-container {
    width: calc(100% - 4rem);
  }
}

.large-container {
  width: calc(100% - 2rem);
  position: relative;
  margin: 0 auto;
  background: #fff;
}
@media (min-width: 900px) {
  .large-container {
    width: calc(100% - 4rem);
  }
}
@media (min-width: 1360px) {
  .large-container {
    width: 1080px;
  }
}

.small-container {
  width: calc(100% - 0rem);
  position: relative;
  margin: 0 auto;
  background: #fff;
}
@media (min-width: 600px) {
  .small-container {
    width: calc(100% - 4rem);
  }
}
@media (min-width: 900px) {
  .small-container {
    width: 780px;
  }
}
@media (min-width: 1160px) {
  .small-container {
    width: 780px;
  }
}
@media (min-width: 1360px) {
  .small-container {
    width: 780px;
  }
}

#home_gallery {
  margin-top: 2rem;
  min-height: 900px;
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  column-gap: 1rem;
  row-gap: 1rem;
}
#home_gallery div {
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
#home_gallery div img {
  width: 100%;
}
#home_gallery div:nth-child(1) {
  grid-column-start: 1;
  grid-column-end: span 5;
  grid-row-start: 1;
  grid-row-end: span 5;
}
#home_gallery div:nth-child(2) {
  grid-column-start: 6;
  grid-column-end: span 2;
  grid-row-start: 1;
  grid-row-end: span 2;
}
#home_gallery div:nth-child(3) {
  grid-column-start: 8;
  grid-column-end: span 2;
  grid-row-start: 1;
  grid-row-end: span 2;
}
#home_gallery div:nth-child(4) {
  grid-column-start: 10;
  grid-column-end: span 3;
  grid-row-start: 1;
  grid-row-end: span 3;
}
#home_gallery div:nth-child(5) {
  grid-column-start: 1;
  grid-column-end: span 3;
  grid-row-start: 5;
  grid-row-end: span 3;
}
#home_gallery div:nth-child(6) {
  grid-column-start: 1;
  grid-column-end: span 3;
  grid-row-start: 5;
  grid-row-end: span 3;
}
#home_gallery div:nth-child(7) {
  grid-column-start: 1;
  grid-column-end: span 3;
  grid-row-start: 5;
  grid-row-end: span 3;
}

section article {
  margin-top: 120px;
  padding: 0 90px;
  text-align: justify;
  display: flex;
  flex-direction: column;
  align-items: center;
}
section article img.thumb {
  width: 7.5 rem;
  height: 7.5rem;
  border-radius: 50%;
}
section article h1 {
  text-align: center;
  margin-bottom: 0.75rem;
  margin-top: 1.25rem;
}
section article .date {
  margin-bottom: 2rem;
  font-style: italic;
}